
.component-input {
  position: relative;
  display: inline-block;
  color: #333;
  margin: 28px 0 7px 0;
  width: 100%;
  max-width: 100%;
}
.component-input.focused.dark input, .component-input.focused.dark textarea {
    border-color: #435f7a;
}
.component-input.focused.dark label {
    color: #435f7a;
}
.component-input.focused.light input, .component-input.focused.light textarea {
    border-color: #fff;
}
.component-input.focused.light label {
    color: #fff;
}
.component-input.focused label {
    top: -12px;
    font-size: .9em;
}
.component-input.has-error .error-icon, .component-input.has-error .error-message {
    opacity: 1;
}
.component-input.has-error.light input, .component-input.has-error.light textarea, .component-input.has-error.dark input, .component-input.has-error.dark textarea {
    border-color: #e74c3c;
}
.component-input.light input, .component-input.light textarea {
    color: #fff;
    border-color: #fff;
}
.component-input.light label {
    color: #fff;
}
.component-input .error-icon, .component-input .error-message {
    color: #e74c3c;
    opacity: 0;
    transition: opacity .2s;
}
.component-input .error-icon {
    position: absolute;
    font-size: 18px;
    top: 10px;
    right: 4px;
}
.component-input .error-message {
    position: absolute;
    width: 100%;
    padding: 4px 0;
    font-size: .9em;
}
.component-input .error-message span {
      display: inline-block;
      white-space: normal;
      min-width: 100%;
      width: 0;
}
.component-input input, .component-input textarea {
    color: #727272;
    font-size: 14px;
    padding: 10px 5px;
    display: block;
    border: none;
    background: transparent;
    border-bottom: 1px solid #727272;
    transition: all .25s;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    outline: 0;
}
.component-input textarea {
    height: 100%;
    resize: none;
    overflow: hidden;
    overflow-y: auto;
    position: absolute;
    top: 0;
    box-sizing: border-box;
}
.component-input .textarea-height-fix {
    max-height: 120px;
    overflow: hidden;
    min-height: 38px;
    padding: 11px 5px;
    box-sizing: border-box;
    visibility: hidden;
    width: 100%;
}
.component-input label {
    position: absolute;
    top: 12px;
    left: 5px;
    pointer-events: none;
    transition: all .25s;
}



