
.component-button {
  display: inline-block;
  position: relative;
  margin: 4px;
  /* Adjust icon position, if there are any icons in the button */
}
.component-button:not(.loading):not(.disabled):hover.white button {
    background-color: #e2e2e2;
    color: #000;
}
.component-button:not(.loading):not(.disabled):hover.blue button {
    background-color: #fff;
    color: #435f7a;
}
.component-button:not(.loading):not(.disabled):hover.red button {
    background-color: #fff;
    color: #e74c3c;
}
.component-button.loading {
    pointer-events: none;
}
.component-button.loading .contents {
      opacity: 0;
}
.component-button.loading .spinner {
      opacity: 1;
}
.component-button.disabled {
    pointer-events: none;
}
.component-button.disabled.white button {
      color: #727272;
}
.component-button.white button {
    color: #333;
    border-color: #e2e2e2;
    background-color: #fff;
}
.component-button.blue button {
    color: #fff;
    border-color: #435f7a;
    background-color: #435f7a;
}
.component-button.red button {
    color: #fff;
    border-color: #e74c3c;
    background-color: #e74c3c;
}
.component-button button {
    padding: 10px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    font-size: 14px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    outline: 0;
    border: 2px solid;
    border-radius: 2px;
    text-transform: uppercase;
    font-weight: 500;
    transition: all .3s;
}
.component-button .spinner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    opacity: 0;
}
.component-button .overlay-disabled {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.4);
}
.component-button .component-icon {
    line-height: 14px;
    vertical-align: middle;
}



