
.route-administration-collection .grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 2fr 1fr;
      grid-template-columns: 2fr 1fr;
  grid-template-areas: "folders actions";
  grid-gap: 20px;
}
@media (max-width: 768px) {
.route-administration-collection .grid {
      -ms-grid-columns: 1fr;
          grid-template-columns: 1fr;
      grid-template-areas: "actions" "folders";
}
}
.route-administration-collection .grid .component-folder-list {
    grid-area: folders;
}
.route-administration-collection .grid .component-scanner-actions {
    grid-area: actions;
}
.route-administration-collection .grid .component-scanner-progress {
    grid-area: folders;
}



