
.component-circle-progress {
  border-radius: 50%;
  background-color: #e2e2e2;
  position: relative;
}
.component-circle-progress .circle {
    width: 100%;
    height: 100%;
    position: relative;
}
.component-circle-progress .circle .mask {
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      position: absolute;
      border-radius: 50%;
      transition: -webkit-transform .5s;
      transition: transform .5s;
      transition: transform .5s, -webkit-transform .5s;
}
.component-circle-progress .circle .mask .fill {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        border-radius: 50%;
        transition: -webkit-transform .5s;
        transition: transform .5s;
        transition: transform .5s, -webkit-transform .5s;
        background-color: #435f7a;
}
.component-circle-progress .inset {
    background-color: #fff;
    position: absolute;
    top: 10px;
    right: 10px;
    left: 10px;
    bottom: 10px;
    border-radius: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.component-circle-progress .inset span {
      font-size: 1.4em;
}
.component-circle-progress .percentage {
    font-size: 1.4em;
    font-weight: bold;
    color: #727272;
}



