
.component-song-list-entry {
  position: relative;
}
.component-song-list-entry:hover .track-number .icons {
    display: block;
}
.component-song-list-entry:hover .track-number span {
    display: none;
}
.component-song-list-entry:hover .track-number .component-playing-indicator {
    display: none;
}
.component-song-list-entry.is-dragging {
    opacity: .4;
}
.component-song-list-entry.is-over td {
    background-color: rgba(114, 114, 114, 0.5);
}
.component-song-list-entry .track-number {
    width: 36px;
    min-width: 36px;
    max-width: 36px;
    position: relative;
    text-align: center;
}
.component-song-list-entry .track-number span {
      opacity: .5;
}
.component-song-list-entry .track-number .component-playing-indicator {
      position: absolute;
      left: 0;
      right: 0;
      top: 50%;
      margin: -8px auto;
}
.component-song-list-entry .track-number .icons {
      display: none;
}
.component-song-list-entry .track-number .icons .component-icon {
        position: absolute;
        top: 50%;
        left: 7px;
        margin-top: -11px;
}
.component-song-list-entry .duration {
    width: 40px;
    min-width: 40px;
}
.component-song-list-entry .options {
    padding: 0 10px;
    min-width: 80px;
    width: 80px;
    position: relative;
}
.component-song-list-entry .options .component-icon {
      float: right;
      margin-left: 4px;
}
.component-song-list-entry a {
    text-decoration: none;
    color: #333;
}
.component-song-list-entry a:hover {
      text-decoration: underline;
}
.component-song-list-entry .component-icon {
    cursor: pointer;
    font-size: 22px;
    opacity: .6;
    transition: opacity, .2s;
}
.component-song-list-entry .component-icon:hover {
      opacity: 1;
      color: #435f7a;
}



