
.component-logo {
  position: relative;
  text-align: center;
}
.component-logo a {
    color: #fff;
    text-decoration: none;
}
.component-logo a h1 {
      position: absolute;
      top: 20px;
      font-weight: 500;
      left: 50px;
      color: #27ae60;
      font-size: 24px;
}
.component-logo a img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: auto;
}



