
@-webkit-keyframes spinner {
0% {
    stroke-dashoffset: 366.66%;
    stroke-dasharray: 390%;
}
50% {
    stroke-dashoffset: 166.66%;
    stroke-dasharray: 390%;
}
100% {
    stroke-dashoffset: 366.66%;
    stroke-dasharray: 390%;
}
}
@keyframes spinner {
0% {
    stroke-dashoffset: 366.66%;
    stroke-dasharray: 390%;
}
50% {
    stroke-dashoffset: 166.66%;
    stroke-dasharray: 390%;
}
100% {
    stroke-dashoffset: 366.66%;
    stroke-dasharray: 390%;
}
}
@-webkit-keyframes rotation {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotation {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.component-spinner {
  margin: 0 auto;
  -webkit-animation: rotation 0.8s infinite linear;
          animation: rotation 0.8s infinite linear;
}
.component-spinner.sm {
    width: 32px;
    height: 32px;
}
.component-spinner.md {
    width: 48px;
    height: 48px;
}
.component-spinner.lg {
    width: 64px;
    height: 64px;
}
.component-spinner.light circle {
    stroke: #fff;
}
.component-spinner.dark circle {
    stroke: #727272;
}
.component-spinner svg {
    width: 100%;
    height: 100%;
}
.component-spinner svg circle {
      fill: transparent;
      stroke-width: 5%;
      -webkit-animation: spinner 2s infinite;
              animation: spinner 2s infinite;
}



