
.component-side-bar-container {
  position: fixed;
  width: 250px;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  background-color: #34495e;
  transition: width .4s;
}
@media (max-width: 1024px) {
.component-side-bar-container {
      width: 300px;
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
      transition: width .4s, -webkit-transform .4s;
      transition: width .4s, transform .4s;
      transition: width .4s, transform .4s, -webkit-transform .4s;
}
.component-side-bar-container.visible {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
}
}
@media (max-width: 480px) {
.component-side-bar-container {
      width: 100vw;
}
}
.component-side-bar-container .logo-container {
    position: relative;
    text-align: center;
    height: 48px;
    margin: 20px 20px 0;
}
.component-side-bar-container .logo-container a {
      color: #fff;
      text-decoration: none;
}
.component-side-bar-container .logo-container a h1 {
        position: absolute;
        top: 20px;
        font-weight: 500;
        left: 50px;
        color: #27ae60;
        font-size: 24px;
}
.component-side-bar-container .logo-container a img {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: auto;
}
.component-side-bar-container .close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
    visibility: hidden;
}
@media (max-width: 1024px) {
.component-side-bar-container .close-button {
        visibility: visible;
}
}
.component-side-bar-container .close-button .component-icon {
      color: #fff;
      font-size: 32px;
}
.component-side-bar-container .search-container {
    margin: 0 10px;
}
.component-side-bar-container .nav-container {
    margin: 7px auto;
}
.component-side-bar-container ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.component-side-bar-container ul li {
      position: relative;
      color: #fff;
}
.component-side-bar-container ul li:hover {
        color: #e74c3c;
}
.component-side-bar-container ul li.active {
        color: #fff;
}
.component-side-bar-container ul li.active:hover {
          color: #e74c3c;
}
.component-side-bar-container ul li.has-icon a {
        padding-left: 44px;
}
.component-side-bar-container ul li a {
        color: inherit;
        text-decoration: none;
        display: block;
        padding: 10px 10px 10px 20px;
        transition: all .2s;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
}
.component-side-bar-container ul li a:hover, .component-side-bar-container ul li a.active {
          text-decoration: none;
          background-color: #435f7a;
}
.component-side-bar-container ul li .component-icon {
        color: inherit;
        position: absolute;
        top: 8px;
        left: 16px;
        transition: color .2s;
}
.component-side-bar-container .user-container {
    color: #fff;
    width: 100%;
    text-align: center;
    padding-top: 14px;
}
.component-side-bar-container .user-container p {
      padding: 0 10px 14px 10px;
}
.component-side-bar-container .user-container .actions {
      width: 50%;
      margin: 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
          justify-content: space-around;
}
.component-side-bar-container .user-container .actions a {
        color: #fff;
        transition: color .2s;
}
.component-side-bar-container .user-container .actions a :hover {
          color: #e74c3c;
}



