
.component-folder-list .folder {
  padding: 10px;
  background-color: #435f7a;
  color: white;
  border-radius: 2px;
  margin-bottom: 3px;
}
.component-folder-list .folder .folder-remove-icon {
    cursor: pointer;
    float: right;
    transition: color, .3s;
    margin-top: 2px;
}
.component-folder-list .folder .folder-remove-icon:hover {
      color: #e74c3c;
}
.component-folder-list .folder .folder-checkbox {
    margin: -5px 0 !important;
}
.component-folder-list .folder p {
    margin-left: 40px;
    padding: 5px;
}
.component-folder-list .no-folders {
  margin-top: 14px;
}



