
.component-player-controls {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  padding: 7px 0;
  background: #34495e;
}
.component-player-controls button {
    text-align: center;
    cursor: pointer;
    border: none;
    border-radius: 50px;
    background: transparent;
    vertical-align: middle;
    transition: opacity .2s;
    padding: 0 7px;
}
.component-player-controls button:hover {
      opacity: 1;
}
.component-player-controls button:focus {
      outline: 0;
}
.component-player-controls button:disabled {
      opacity: .5;
      cursor: default;
}
.component-player-controls button.button-play .component-icon {
      font-size: 48px;
}
.component-player-controls button .component-icon {
      color: #fff;
}
.component-player-controls .progress {
    position: absolute;
    top: -5px;
    height: 5px;
    width: 100%;
    cursor: pointer;
    z-index: 33;
}
.component-player-controls .progress .seek-bar {
      width: 100%;
      height: 100%;
      background: rgba(114, 114, 114, 0.5);
}
.component-player-controls .progress .play-bar {
      height: 100%;
      background: #e74c3c;
}
.component-player-controls .volume-controls {
    display: inline;
}
.component-player-controls .volume-controls .volume-bar-container {
      position: absolute;
      height: 20px;
      background-color: #34495e;
      bottom: 12px;
      right: 12px;
      width: 70%;
      z-index: 10;
      padding-top: 7px;
      visibility: hidden;
      cursor: pointer;
      overflow: hidden;
}
.component-player-controls .volume-controls .volume-bar-container .volume-bar {
        width: 100%;
        height: 6px;
        background: rgba(114, 114, 114, 0.5);
}
.component-player-controls .volume-controls .volume-bar-container .volume-bar .volume-bar-value {
          background: #fff;
          width: 50%;
          height: 100%;
}
.component-player-controls .volume-controls .volume-bar-container .hover {
        visibility: visible;
}
.component-player-controls .volume-controls:hover .volume-bar-container {
      visibility: visible;
}
.component-player-controls .advanced-controls {
    text-align: center;
}
.component-player-controls .advanced-controls button i.active {
      color: #e74c3c;
}
.component-player-controls .timers {
    position: absolute;
    top: -27px;
    left: 0;
    right: 0;
    opacity: 0;
    transition: opacity .5s;
}
.component-player-controls .timers div {
      position: absolute;
      top: 0;
      padding: 3px;
      color: #fff;
      background: rgba(114, 114, 114, 0.5);
}
.component-player-controls .timers .current-time {
      left: 0;
}
.component-player-controls .timers .duration {
      right: 0;
}



