
.component-carousel {
  width: 100%;
  margin: 0 auto;
}
.component-carousel .carousel-viewport {
    position: relative;
    overflow: hidden;
}
.component-carousel .carousel-viewport:hover .button.visible {
      visibility: visible;
      opacity: 1;
      transition: opacity .2s;
}
.component-carousel .carousel-viewport .carousel-track {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
      transition: -webkit-transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
      transition: transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
      transition: transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.component-carousel .carousel-viewport .carousel-track > div {
        padding: 10px;
}
.component-carousel .carousel-viewport .button {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 48px;
      height: 48px;
      margin: auto 0;
      border-radius: 100%;
      border: 1px solid #e2e2e2;
      background: #fff;
      cursor: pointer;
      box-shadow: 2px 2px 4px #888;
      opacity: 0;
      visibility: hidden;
      transition: opacity .4s, visibility 0s .4s;
}
.component-carousel .carousel-viewport .button.next {
        right: 8px;
}
.component-carousel .carousel-viewport .button.previous {
        left: 8px;
}
.component-carousel .carousel-viewport .button .component-icon {
        display: block;
        color: #727272;
        font-size: 32px;
        width: 100%;
        height: 100%;
        text-align: center;
        padding-top: 6px;
}



