
.component-create-playlist-modal .add-image {
  position: relative;
  width: 160px;
  height: 160px;
  margin: 20px auto;
  border-radius: 100%;
  overflow: hidden;
}
.component-create-playlist-modal .add-image:hover .hover-overlay {
    opacity: 1;
}
.component-create-playlist-modal .add-image .hover-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    opacity: 0;
    transition: opacity .4s;
}
.component-create-playlist-modal .add-image .hover-overlay input {
      position: absolute;
      top: 0;
      left: -10000px;
}
.component-create-playlist-modal .add-image .hover-overlay label {
      position: relative;
      cursor: pointer;
      width: 100%;
      height: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.component-create-playlist-modal .add-image .hover-overlay .component-icon {
      font-size: 32px;
      margin-bottom: 10px;
}
.component-create-playlist-modal .add-image img {
    width: 100%;
    height: auto;
}



