
.component-change-password .change-password-form {
  max-width: 400px;
  margin: 10px auto;
}
.component-change-password .change-password-form .component-button {
    float: right;
    margin: 24px 0;
}
.component-change-password .change-error {
  text-align: center;
  color: #e74c3c;
}
.component-change-password .password-changed {
  text-align: center;
  margin: 60px auto;
}
.component-change-password .password-changed .component-icon {
    font-size: 64px;
    opacity: .5;
    color: #27ae60;
}
.component-change-password .password-changed p {
    margin-top: 10px;
    font-size: 1.4em;
}
.component-change-password h2 {
  padding: 20px 10px 0;
  color: #727272;
}



