
.route-album .album-details {
  position: relative;
  height: 200px;
  margin-bottom: 30px;
}
.route-album .album-details .info {
    width: calc(100% - 200px);
    padding: 10px 20px 20px 0;
}
.route-album .album-details .art {
    position: absolute;
    top: 0;
    right: 0;
    width: 200px;
    height: 200px;
    box-shadow: 2px 2px 4px;
}
.route-album .album-details .actions {
    position: absolute;
    bottom: 0;
    left: 0;
}



