
.component-scroll-top-button {
  background: #435f7a;
  width: 48px;
  height: 48px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  border-radius: 100%;
  color: #fff;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  cursor: pointer;
  box-shadow: 0 0 3px #333;
  z-index: 999;
}
.component-scroll-top-button .component-icon {
    font-size: 48px;
}



