
.component-header {
  position: relative;
  padding: 14px;
  background-color: #435f7a;
  color: #fff;
}
@media (max-width: 1024px) {
.component-header {
      padding-left: 68px;
}
}
.component-header .component-hamburger-button {
    position: absolute;
    top: 10px;
    left: 10px;
    visibility: hidden;
}
@media (max-width: 1024px) {
.component-header .component-hamburger-button {
        visibility: visible;
}
}
.component-header h1 {
    font-size: 26px;
    margin: 0;
}
.component-header a {
    color: #fff;
}



