
.component-player-art {
  position: relative;
  width: 100%;
  height: 250px;
  bottom: 92px;
  overflow: hidden;
}
.component-player-art .art {
    width: 100%;
    height: 100%;
}
.component-player-art .art .background {
      position: absolute;
      background: #e2e2e2 center center;
      background-size: cover;
      width: 100%;
      height: 100%;
}
.component-player-art .art .background.default {
        background-image: url("../../../assets/img/album-default.jpg");
}
.component-player-art .details {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    opacity: 0;
    transition: opacity .5s;
}
.component-player-art .details ul {
      margin: 0;
      padding: 0;
      list-style: none;
      width: 100%;
}
.component-player-art .details ul li, .component-player-art .details ul a {
        max-width: 100%;
}
.component-player-art .details ul li {
        margin-bottom: 7px;
        display: block;
}
.component-player-art .details ul li a {
          display: inline-block;
          padding: 7px 14px;
          color: #fff;
          background: rgba(114, 114, 114, 0.75);
          cursor: pointer;
          text-decoration: none;
}
.component-player-art .drop-overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #fff;
    font-size: 1.4em;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.4);
}
.component-player-art .drop-overlay .component-icon {
      font-size: 3em;
      margin-bottom: 40px;
}



