
.route-login {
  max-width: 400px;
  margin: 0 auto;
}
.route-login .feedback-container {
    position: relative;
    margin-top: 20px;
    height: 40px;
    width: 100%;
}
.route-login .feedback-container .login-spinner {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
}
.route-login .feedback-container .login-error {
      color: #e74c3c;
      text-align: center;
}
.route-login .login-logo {
    width: 140px;
    margin: 0 auto;
}
.route-login .login-logo img {
      margin: 60px 0 20px;
      width: 100%;
}
.route-login .login-logo h1 {
      color: #27ae60;
      text-align: center;
}
.route-login form {
    margin: 0 0 60px 0;
}
.route-login .component-button {
    float: right;
    margin: 14px 0;
}
.route-login .component-logo {
    width: 120px;
    height: 120px;
    margin: 40px auto;
}
.route-login footer {
    color: #fff;
    margin: 120px auto 0;
    text-align: center;
}



