
.component-default-modal {
  position: relative;
  background: #fff;
  border: 1px solid #e2e2e2;
  padding: 60px;
  min-width: 400px;
  max-width: 95vw;
}
@media (max-width: 480px) {
.component-default-modal {
      width: 100vw;
      height: 100vh;
      max-width: 100vw;
}
}
.component-default-modal .close-icon {
    position: absolute;
    color: #727272;
    cursor: pointer;
    top: 16px;
    right: 16px;
}
.component-default-modal .close-icon .component-icon {
      font-size: 24px;
}
@media (max-width: 1024px) {
.component-default-modal .close-icon .component-icon {
          font-size: 32px;
}
}
.component-default-modal .modal-content .modal-headline {
    text-align: center;
}
.component-default-modal .modal-content .modal-text {
    padding: 30px 0;
}
.component-default-modal .modal-content .modal-buttons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
    padding-top: 20px;
}
.component-default-modal .modal-content .modal-buttons .component-button {
      margin-left: 10px;
}



