
.component-album-art {
  width: 100%;
  height: 100%;
  position: relative;
}
.component-album-art .background {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e2e2e2 center center;
    background-size: cover;
}
.component-album-art .background.default {
      background-image: url("../../../assets/img/album-default.jpg");
}
.component-album-art:hover .overlay {
    opacity: 1;
}
.component-album-art.playing .overlay {
    opacity: 1;
}
.component-album-art.playing-album .overlay:hover .component-playing-indicator {
    display: none;
}
.component-album-art.playing-album .overlay:hover .icons {
    display: block;
}
.component-album-art.playing-album .overlay .icons {
    display: none;
}
.component-album-art.playing-album .overlay .component-playing-indicator {
    display: block;
}
.component-album-art .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    opacity: 0;
    transition: opacity .5s;
}
.component-album-art .overlay .play-button {
      cursor: pointer;
}
.component-album-art .overlay .play-button .component-icon {
        color: #fff;
        font-size: 48px;
}



