
.component-header-menu {
  margin-bottom: 28px;
}
.component-header-menu ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    list-style: none;
    margin: 0;
    padding: 0;
    left: 0;
    right: 0;
    top: 0;
}
.component-header-menu ul li {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      text-align: center;
      text-transform: uppercase;
      border-bottom: 2px solid #727272;
      position: relative;
      z-index: 1;
      transition: color .3s;
}
.component-header-menu ul li:after {
        content: "";
        position: absolute;
        top: calc(100% - 12px);
        left: 0;
        right: 0;
        margin: auto;
        background-color: #435f7a;
        width: 24px;
        height: 24px;
        border-bottom: 2px solid #27ae60;
        border-left: 2px solid #27ae60;
        opacity: 0;
        z-index: -1;
        transition: all .3s;
        -webkit-transform: translateY(-12px) rotate(-45deg);
                transform: translateY(-12px) rotate(-45deg);
}
.component-header-menu ul li:before {
        content: "";
        width: 0;
        height: 2px;
        position: absolute;
        bottom: -2px;
        left: 0;
        right: 0;
        margin: auto;
        background: #e74c3c;
        z-index: -2;
        transition: width .3s;
}
.component-header-menu ul li.active {
        color: #e74c3c;
}
.component-header-menu ul li.active:before {
          width: 100%;
}
.component-header-menu ul li.active:hover:after, .component-header-menu ul li.active:after {
          opacity: 1;
          -webkit-transform: rotate(-45deg);
                  transform: rotate(-45deg);
}
.component-header-menu ul li.active:after {
          border-color: #e74c3c;
}
.component-header-menu ul li a {
        display: block;
        padding: 10px;
        text-decoration: none;
        background-color: #435f7a;
        color: #fff;
}



