
.component-playlist-header .name-group {
  display: inline-block;
  position: relative;
}
.component-playlist-header .name-group .playlist-actions {
    position: absolute;
    top: 8px;
    right: -60px;
}
.component-playlist-header .name-group .playlist-actions .component-icon {
      cursor: pointer;
      transition: color .2s;
}
.component-playlist-header .name-group .playlist-actions .component-icon:hover {
        color: #e74c3c;
}
.component-playlist-header .description {
  font-style: italic;
}



