
.component-check-box {
  margin: 14px 0;
  padding: 10px 10px 10px 36px;
  position: relative;
  display: inline-block;
}
.component-check-box.light .component-icon, .component-check-box.light label {
    color: #fff;
}
.component-check-box label {
    color: #333;
    cursor: pointer;
}
.component-check-box .component-icon {
    position: absolute;
    color: #435f7a;
    top: 6px;
    left: 5px;
}
.component-check-box input {
    visibility: hidden;
    display: none;
}



