
.component-card {
  background: #fff;
  position: relative;
  box-shadow: 0 0 2px #333;
  overflow: hidden;
}
.component-card .main {
    background: #fff;
    color: #333;
    padding: 7px;
}
.component-card .main .caption p {
      max-width: calc(135px - 14px);
      font-weight: 500;
}
.component-card .main .details {
      margin-top: 5px;
      max-width: calc(135px * 0.7);
}
.component-card .main .icon {
      position: absolute;
      right: 7px;
      bottom: 7px;
}
.component-card .main .icon .component-icon {
        position: absolute;
        font-size: 14px;
        left: -16px;
        top: 2px;
}
.component-card .art-container {
    position: relative;
    width: 100%;
    padding-top: 100%;
}
.component-card .art-container .art {
      position: absolute;
      background: #e2e2e2;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
}
.component-card .art-container .art img {
        width: 100%;
        height: 100%;
}
.component-card a {
    text-decoration: none;
    color: #333;
}



