
.component-song-popover {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 240px;
  background-color: #fff;
  cursor: default;
  max-height: 300px;
  overflow: auto;
  z-index: 1;
  box-shadow: 2px 2px 4px;
}
.component-song-popover .header {
    position: relative;
    border-bottom: 1px solid #e2e2e2;
}
.component-song-popover .header .art {
      position: absolute;
      left: 0;
      top: 0;
      width: 59px;
      height: 59px;
}
.component-song-popover .header .art img {
        position: absolute;
        width: 100%;
        height: 100%;
}
.component-song-popover .header .title {
      padding: 10px 10px 10px 70px;
}
.component-song-popover .header .title p:first-child {
        margin: 0;
        font-size: 1.2em;
}
.component-song-popover .content ul,
  .component-song-popover .playlists ul {
    padding: 0;
    margin: 0;
    list-style: none;
    color: #727272;
    cursor: pointer;
}
.component-song-popover .content ul li,
    .component-song-popover .playlists ul li {
      padding: 14px;
}
.component-song-popover .content ul li:last-child,
      .component-song-popover .playlists ul li:last-child {
        border-bottom: none;
}
.component-song-popover .content ul li:hover,
      .component-song-popover .playlists ul li:hover {
        background-color: #e2e2e2;
        color: #34495e;
}
.component-song-popover .playlists {
    position: absolute;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    background-color: #fff;
}
.component-song-popover .playlists.visible {
      -webkit-transform: translatex(0);
              transform: translatex(0);
}
.component-song-popover .playlists .back {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 40px;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      border-bottom: 1px solid #e2e2e2;
}
.component-song-popover .playlists .back .component-icon {
        padding: 10px;
}
.component-song-popover .playlists .playlist .component-icon {
      line-height: 14px;
      vertical-align: middle;
}



